import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import uniTag from "@/components/uni-tag/uni-tag.vue"
import http from '@/components/utils/http.js'
import {formatDate} from '@/components/date.js'
import {
	mapState
} from 'vuex'
export default {
	components: {
		uniTag,
		uniLoadMore
	},
	filters: {
		formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd')
		}
	},
	computed: mapState(['token', 'user_id','group_id']),
	data() {
		return {
			itemList: [],
			keywordinput: '',
			keyword: '',
			infoType: 0,
			userid: 0,
			page: 1,
			nextPage: 1,
			status: 'more',//more   显示文字加载更多  loading  显示文字 正在加载   nomore  显示没有更多了
			groupid: false,
			lodingLock: false,
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '———— 杭州市安庆商会 ————'
			},
			itemid: ''
		}
	},
	onLoad() {
		let that = this
		uni.showLoading({
			title: '加载中...',
		});
		
		this.loadData(() => {
			//加载完成的回调，隐藏加载框
			uni.hideLoading();
		});
		
		
		uni.$on("editInfo", () => {
			that.itemList = [];
			// that.keywordinput: '';
			// that.keyword: '';
			// that.infoType= 0;
			that.page= 1;
			that.nextPage= 1;
			that.status= 'more';
			that.loadData(() => {	
			});
		});
	},
	onUnload(e) {
		uni.$off("editInfo", () => {
			//loadData();
		});
	},
	//下拉触底
	onReachBottom() {
		let that = this;
		if (that.nextPage > 0) {
			that.loadData(() => {
		
			});
		} else {
		
		}	
	},
	// onShareAppMessage(res) {
	// 	if (res.from === 'button') {//来自页面内分享按钮
	// 		console.log(res.target)
	// 	}
	// 	return {
	// 	  title: '我的供求',
	// 	  path: '/pages/gongqiu/gongqiu'
	// 	}
	// },
	methods: {
		//加载供求数据
		loadData(collback) {
			if (this.lodingLock) {
				return;
			}
			this.lodingLock = true;
			this.status = "loading"
			let that = this;
			let param = {
				page: that.nextPage,
				keyword: that.keyword,
				type: that.infoType,
				uid: this.user_id
			};
			http.apiRequest({
				api: "/api/info/infoList",
				data: param,
				secuss: (res, httpStatus) => {
					that.lodingLock = false;
					if (res.code == "1") {
						that.nextPage = res.data.nextPage;
						that.page = res.data.page;
						that.itemList = that.itemList.concat(res.data.rows);
		
						if (that.nextPage > 0) {
							that.status = "more";
						} else {
							that.status = 'nomore';
						}
		
						if (typeof(collback) == "function") {
							collback();
						}
					} else {
						if (that.nextPage > 0) {
							that.status = "more"
						} else {
							that.status = "nomore"
						}
						uni.showToast({
							title: res.msg,
							duration: 1500,
							icon: "none"
						});
						if (typeof(collback) == "function") {
							collback();
						}
					}
				},
				error: (res, httpStatus) => {
					//接口调用失败，可能是网络故障，可能是接口报错
					if (that.nextPage > 0) {
						that.status = "more"
					} else {
						that.status = "nomore"
					}
					that.lodingLock = false;
					uni.showToast({
						title: "接口调用失败，请检查网络",
						duration: 1500,
						icon: "none"
					});
					if (typeof(collback) == "function") {
						collback();
					}
				},
			});
		},
		openshare(e) {
			console.log(e)
			var itemid = e.currentTarget.dataset.itemid
			this.itemid = itemid
		},
		openinfo(e) {
			console.log(e)
			var itemid = e.currentTarget.dataset.itemid
			// console.log(userid)
			uni.navigateTo({
				// 传递 userid 在详情页接收
				url: '/pages/gongqiu/gongqiuDetail/gongqiuDetail?itemid=' + itemid
			});
		},
		//搜索按钮
		search(e) {
			let that = this;
			that.keyword = that.keywordinput;
			that.nextPage = 1;
			that.itemList = [];
			that.loadData(() => {
		
			});
		},
		revise(e) {
			console.log(e)
			var usersid = e.currentTarget.dataset.usersid
			var title = e.currentTarget.dataset.title
			var content = e.currentTarget.dataset.content
			uni.navigateTo({
				url: `/pages/gongqiu/revise/revise?usersid=${usersid}&title=${title}&contont=${content}`
			});
		},
		// share(e) {
		// 	console.log(e)
		// 	var that=this;
		// 	var item_index = e.currentTarget.dataset.index;
		// },
		delect(e) {
			var that=this;
			var usersid = e.currentTarget.dataset.usersid;
			var item_index = e.currentTarget.dataset.index;
			var token=this.token;
			this.userid = usersid
			uni.showModal({
				title: '提示',
				content: '你确定要删除吗？删除后将不能找回！',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						let opts = {
							url: '/api/info/infoRemove',
							method: 'POST',
							header: that.token
						};
						let param = {
							ids: usersid
						};
						http.httpTokenRequest(opts, param).then(res => {
							console.log(res.data);
							if (res.data.code == 1) {
								that.itemList.splice(item_index, 1);
								uni.showToast({
									title: "删除成功"
								});
								uni.switchTab({
									url: '/pages/user/myinfo/myinfo'
								});
							} else {
								uni.showToast({
									title: "加载失败..."
								});
							}
						}, error => {
							console.log('error=>', error)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		release() {
			uni.navigateTo({
				url: '/pages/gongqiu/release/release'
			});
		}
	}
}
